<template>
  <div class="ExperimentPage py-3">
    <div class="container-fluid">
      <div class="row">
        <div class="spectrometer">
          <v-stage ref="stage" id="stage" :config="configKonva">
            <v-layer ref="layer">
              <v-image
                :config="configSlit"
              ></v-image>
              <v-image
                 v-if="toggleLight == true && slit <=10 && slit>=5 && telescopeFocus == 0 && Grating == false"
                :config="configSlitLight"
              ></v-image>
              <v-image
                v-if="toggleLight == true && slit <=10 && slit>=5 && telescopeFocus == 0 && Grating == true"
                :config="configViolet"
              ></v-image>
              <v-image
                v-if="toggleLight == true && slit <=10 && slit>=5 && telescopeFocus == 0 && Grating == true"
                :config="configBlue"
              ></v-image>
              <v-image
                v-if="toggleLight == true && slit <=10 && slit>=5 && telescopeFocus == 0 && Grating == true"
                :config="configGreen"
              ></v-image>
              <v-image
                v-if="toggleLight == true && slit <=10 && slit>=5 && telescopeFocus == 0 && Grating == true"
                :config="configYellow"
              ></v-image>
              <v-image
                v-if="toggleLight == true && slit <=10 && slit>=5 && telescopeFocus == 0 && Grating == true"
                :config="configViolet2"
              ></v-image>
              <v-image
                v-if="toggleLight == true && slit <=10 && slit>=5 && telescopeFocus == 0 && Grating == true"
                :config="configBlue2"
              ></v-image>
              <v-image
                v-if="toggleLight == true && slit <=10 && slit>=5 && telescopeFocus == 0 && Grating == true"
                :config="configGreen2"
              ></v-image>
              <v-image
                v-if="toggleLight == true && slit <=10 && slit>=5 && telescopeFocus == 0 && Grating == true"
                :config="configYellow2"
              ></v-image>
              <v-image
                :config="configSlitBackground"
              ></v-image>
              <v-image :config="configCollimator"></v-image>
              <v-image :config="configTelescope"></v-image>
              <v-image
                :config="configChavronClockwise"
                @click="telescopeClockwise()"
              ></v-image>
              <v-image
                :config="configChavronAntiClockwise"
                @click="telescopeAntiClockwise()"
              ></v-image>
              <v-image :config="configCalibrate"></v-image>
              <v-image :config="configVernier"></v-image>
              <v-image :config="configTable" v-if="Grating == false"></v-image>
              <v-image :config="configGratingTable" v-else></v-image>
              <v-image
                :config="configSlitTableLeft"
                @click="tableAntiClockwise()"
              ></v-image>
              <v-image
                :config="configSlitTableRight"
                @click="tableClockwise()"
              ></v-image>
              <v-image
                :config="configVernierLeft"
                @click="vernierAntiClockwise()"
              ></v-image>
              <v-image
                :config="configVernierRight"
                @click="vernierClockwise()"
              ></v-image>
              <v-image
                :config="configCalibrateZoom"
              ></v-image>
              <v-image
                :config="configVernierZoom"
              ></v-image>
              <v-image
                v-if="toggleLight == false"
                :config="configLightOff"
              ></v-image>
              <v-image v-else :config="configLightOn"></v-image>
              <v-image
                v-if="toggleLight == false"
                :config="configToggleBtnOff"
                @click="switchLight()"
              ></v-image>
              <v-image
                v-else
                :config="configToggleBtnOn"
                @click="switchLight()"
              ></v-image>
              <v-image
                :config="configSpectrometer"
              ></v-image>
              
              <v-line 
                :config="configLine1"
              ></v-line>
              <v-line 
                :config="configLine2"
              ></v-line>
              <v-line 
                :config="configLineVertical"
              ></v-line>
            </v-layer>
          </v-stage>
        </div>
        <div class="setup">
          <div class="card">
            <div class="card-header">
              Settings
            </div>
            <div class="card-body">
                <ul class="list-group list-group-flush">
                    <li class="list-group-item">
                        <div class="row">
                            <div class="col-lg-6 col-md-12 d-flex align-items-center">
                                <h5 class="card-title pr-3">Telescope Focus : </h5>
                                <input  type="range" min="0" max="10" step="1" v-model="telescopeFocus"> 
                            </div>
                            <div class="col-lg-6 col-md-12 d-flex align-items-center bluredImage" v-bind:style="{ filter: 'blur('+telescopeFocus+'px)' }">
                            </div>
                        </div>
                    </li>
                    <li class="list-group-item">
                        <div class="row">
                            <div class="col-lg-6 col-md-12 d-flex align-items-center">
                                <h5 class="card-title pr-3">Slit : </h5>
                                <input  type="range" min="0" max="100" step="1" v-model="slit"> 
                            </div>
                            <div class="col-lg-6 col-md-12 d-flex align-items-center">
                              <div class="slit d-flex justify-content-center align-items-center">
                                <img src="../assets/images/slit.png" alt="" class="gap" v-bind:style="{ width: slit+'px' }">
                              </div>
                            </div>
                        </div>
                    </li>
                    <li class="list-group-item d-flex align-items-center">
                        <h5 class="card-title pr-3">Grating State: </h5>
                        <button
                            class="btn btn-primary"
                            @click="Grating = !Grating"
                            v-if="Grating == false"
                        >
                            Place Grating
                        </button>
                        <button
                            class="btn btn-danger"
                            @click="Grating = !Grating"
                            v-else
                        >
                            Remove Grating
                        </button>
                    </li>
                    
                </ul>
              
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Experiment",
  props: {},
  data() {
    return {
      toggleLight: false,
      Grating: false,
      telescopeFocus: 7 ,
      slit: 30,

      configKonva: {
        width: 1200,
        height: 600,
      },
      configLightOff: {
        x: 540,
        y: 180,
        image: new Image(),
        width: 75,
        height: 130,
      },
      configLightOn: {
        x: 540,
        y: 180,
        image: new Image(),
        width: 75,
        height: 130,
      },
      configToggleBtnOff: {
        x: 530,
        y: 50,
        image: new Image(),
        width: 90,
        height: 50,
      },
      configToggleBtnOn: {
        x: 530,
        y: 50,
        image: new Image(),
        width: 90,
        height: 50,
      },
      configCollimator: {
        x: 330,
        y: 220,
        image: new Image(),
        width: 180,
        height: 100,
      },
      configTelescope: {
        x: 280,
        y: 270,
        image: new Image(),
        width: 180,
        height: 100,
        rotation: 0,
        offset: {
          x: 250,
          y: 50,
        },
      },
      configVernier: {
        x: 276,
        y: 270,
        image: new Image(),
        width: 180,
        height: 180,
        rotation: 0,
        offset: {
          x: 90,
          y: 90,
        },
      },
      configTable: {
        x: 278,
        y: 270,
        image: new Image(),
        width: 80,
        height: 80,
        rotation: 0,
        offset: {
          x: 40,
          y: 40,
        },
      },
      configGratingTable: {
        x: 278,
        y: 270,
        image: new Image(),
        width: 80,
        height: 80,
        rotation: 0,
        offset: {
          x: 40,
          y: 40,
        },
      },
      configChavronClockwise: {
        x: 280,
        y: 270,
        image: new Image(),
        width: 20,
        height: 30,
        rotation: 0,
        offset: {
          x: 250,
          y: 50,
        },
      },
      configChavronAntiClockwise: {
        x: 280,
        y: 270,
        image: new Image(),
        width: 20,
        height: 30,
        rotation: 0,
        offset: {
          x: 250,
          y: -30,
        },
      },
      configCalibrate: {
        x: 277,
        y: 270,
        image: new Image(),
        width: 155,
        height: 155,
        rotation: 0,
        offset: {
          x: 155 / 2,
          y: 155 / 2,
        },
      },
      configSlitTableLeft: {
        x: 279,
        y: 269,
        image: new Image(),
        width: 20,
        height: 30,
        rotation: -95,
        offset: {
          x: -12,
          y: 5,
        },
      },
      configSlitTableRight: {
        x: 279,
        y: 272,
        image: new Image(),
        width: 20,
        height: 30,
        rotation: 80,
        offset: {
          x: -12,
          y: 20,
        },
      },
      configVernierLeft: {
        x: 280,
        y: 270,
        image: new Image(),
        width: 20,
        height: 30,
        rotation: 0,
        offset: {
          x: -40,
          y: -20,
        },
      },
      configVernierRight: {
        x: 278,
        y: 270,
        image: new Image(),
        width: 20,
        height: 30,
        rotation: 0,
        offset: {
          x: -40,
          y: 50,
        },
      },
      configCalibrateZoom: {
        x: 825,
        y: 250,
        image: new Image(),
        width: 400,
        height: 400,
        rotation: 0,
        offset: {
          x: 400 / 2,
          y: 400 / 2,
        },
      },
      configVernierZoom: {
        x: 647,
        y: 95,
        image: new Image(),
        width: 520,
        height: 500,
        rotation: 0,
        offset: {
          x: 155 / 2,
          y: 155 / 2,
        },
      },
      configSpectrometer: {
        x: 750,
        y: 500,
        image: new Image(),
        width: 320,
        height: 140,
        rotation: 0,
        offset: {
          x: 155 / 2,
          y: 155 / 2,
        },
      },
      configSlit: {
        x: 450,
        y: 470,
        image: new Image(),
        width: 150,
        height: 150,
        rotation: 0,
        offset: {
          x: 155 / 2,
          y: 155 / 2,
        },
      },
      configSlitBackground: {
        x: 412,
        y: 432,
        image: new Image(),
        width: 370,
        height: 370,
        rotation: 0,
        offset: {
          x: 300 / 2,
          y: 300 / 2,
        },
      },
      configSlitLight: {
        x: 524,
        y: 470,
        image: new Image(),
        width: 5,
        height: 150,
        rotation: 0,
        offset: {
          x: 155 / 2,
          y: 155 / 2,
        },
      },
      configLine1: {
        x: 470,
        y: 400,
        points: [10, 0, 200, 40],
        stroke: 'gray',
        strokeWidth: 2,
        lineJoin: 'round',
        /*
         * line segments with a length of 33px
         * with a gap of 10px
         */
        dash: [33, 10],
      },
      configLine2: {
        x: 470,
        y: 400,
        points: [0, 139, 200, 40],
        stroke: 'gray',
        strokeWidth: 2,
        lineJoin: 'round',
        /*
         * line segments with a length of 33px
         * with a gap of 10px
         */
        dash: [33, 10],
      },
      configLineVertical: {
        x: 448,
        y: 400,
        points: [0, 140, 0, 0],
        stroke: 'gray',
        strokeWidth: 2,
        lineJoin: 'round',
        /*
         * line segments with a length of 33px
         * with a gap of 10px
         */
        dash: [33, 10],
      },
      configViolet: {
        x: 563,
        y: 470,
        image: new Image(),
        width: 5,
        height: 150,
        rotation: 0,
        opacity: 1,
        offset: {
          x: 155 / 2,
          y: 155 / 2,
        },
      },
      configBlue: {
        x: 583,
        y: 470,
        image: new Image(),
        width: 5,
        height: 150,
        rotation: 0,
        opacity: 1,
        offset: {
          x: 155 / 2,
          y: 155 / 2,
        },
      },
      configGreen: {
        x: 528,
        y: 470,
        image: new Image(),
        width: 5,
        height: 150,
        rotation: 0,
        opacity: 1,
        offset: {
          x: 5 / 2,
          y: 155 / 2,
        },
      },
      configYellow: {
        x: 548,
        y: 470,
        image: new Image(),
        width: 5,
        height: 150,
        rotation: 0,
        opacity: 1,
        offset: {
          x: 5 / 2,
          y: 155 / 2,
        },
      },
      configViolet2: {
        x: 483,
        y: 475,
        image: new Image(),
        width: 5,
        height: 150,
        rotation: 0,
        opacity: 1,
        offset: {
          x: 155 / 2,
          y: 155 / 2,
        },
      },
      configBlue2: {
        x: 463,
        y: 475,
        image: new Image(),
        width: 5,
        height: 150,
        rotation: 0,
        opacity: 1,
        offset: {
          x: 155 / 2,
          y: 155 / 2,
        },
      },
      configGreen2: {
        x: 368,
        y: 467,
        image: new Image(),
        width: 5,
        height: 150,
        rotation: 0,
        opacity: 1,
        offset: {
          x: 5 / 2,
          y: 150 / 2,
        },
      },
      configYellow2: {
        x: 348,
        y: 467,
        image: new Image(),
        width: 5,
        height: 150,
        rotation: 0,
        opacity: 1,
        offset: {
          x: 5 / 2,
          y: 150 / 2,
        },
      },
    };
  },
  mounted: function () {
    this.configLightOff.image.src = require("@/assets/images/light_off.png");
    this.configLightOn.image.src = require("@/assets/images/light_on.png");
    this.configToggleBtnOff.image.src = require("@/assets/images/toggle_button_off.png");
    this.configToggleBtnOn.image.src = require("@/assets/images/toggle_button_on.png");
    this.configCollimator.image.src = require("@/assets/images/DG/collimator.png");
    this.configTelescope.image.src = require("@/assets/images/DG/telescope.png");
    this.configVernier.image.src = require("@/assets/images/DG/vernier.png");
    this.configTable.image.src = require("@/assets/images/DG/table.png");
    this.configGratingTable.image.src = require("@/assets/images/DG/gratingTable.png");
    this.configChavronClockwise.image.src = require("@/assets/images/chavron.png");
    this.configChavronAntiClockwise.image.src = require("@/assets/images/chavron2.png");
    this.configCalibrate.image.src = require("@/assets/images/DG/calibrate.png");
    this.configSlitTableLeft.image.src = require("@/assets/images/left.png");
    this.configSlitTableRight.image.src = require("@/assets/images/right.png");
    this.configVernierLeft.image.src = require("@/assets/images/vleft.png");
    this.configVernierRight.image.src = require("@/assets/images/vright.png");
    this.configCalibrateZoom.image.src = require("@/assets/images/DG/calibrate.png");
    this.configVernierZoom.image.src = require("@/assets/images/DG/calibrate3.png");
    this.configSpectrometer.image.src = require("@/assets/images/DG/spectrometer.png");
    this.configSlit.image.src = require("@/assets/images/slitWrapper.png");
    this.configSlitBackground.image.src = require("@/assets/images/slitWrapperBackground.png");
    this.configSlitLight.image.src = require("@/assets/images/slit.png");
    this.configViolet.image.src = require("@/assets/images/violet.png");
    this.configBlue.image.src = require("@/assets/images/blue.png");
    this.configGreen.image.src = require("@/assets/images/green.png");
    this.configYellow.image.src = require("@/assets/images/yellow.png");
    this.configViolet2.image.src = require("@/assets/images/violet.png");
    this.configBlue2.image.src = require("@/assets/images/blue.png");
    this.configGreen2.image.src = require("@/assets/images/green.png");
    this.configYellow2.image.src = require("@/assets/images/yellow.png");
  },
  methods: {

    switchLight() {
      this.toggleLight = !this.toggleLight;
    },

    ////////////////////////////////////////////////////////////////////////////////////Telescope rotation
    telescopeClockwise() {
      if (this.configChavronClockwise.rotation >= 90) {
        this.configChavronClockwise.rotation = 90;
      } else {
        this.configChavronClockwise.rotation = this.configChavronClockwise.rotation + 5;
        this.configTelescope.rotation = this.configTelescope.rotation + 5;
        this.configChavronAntiClockwise.rotation = this.configChavronAntiClockwise.rotation + 5;
        this.configCalibrate.rotation = this.configCalibrate.rotation + 5;
        this.configCalibrateZoom.rotation = this.configCalibrateZoom.rotation + 5;
        this.configSlitLight.x = this.configSlitLight.x + 30;

        this.configViolet.x = this.configViolet.x + 10;
        this.configBlue.x = this.configBlue.x + 10;
        this.configGreen.x = this.configGreen.x + 10;
        this.configYellow.x = this.configYellow.x + 10;
        this.configViolet2.x = this.configViolet2.x + 10;
        this.configBlue2.x = this.configBlue2.x + 10;
        this.configGreen2.x = this.configGreen2.x + 10;
        this.configYellow2.x = this.configYellow2.x + 10;
        if(this.configTelescope.rotation > 0){
          this.configViolet.opacity = 0;
          this.configBlue.opacity = 0;
          this.configGreen.opacity = 0;
          this.configYellow.opacity = 0;

          this.configViolet2.opacity = 1;
          this.configBlue2.opacity = 1;
          this.configGreen2.opacity = 1;
          this.configYellow2.opacity = 1;
        }
        else if (this.configTelescope.rotation == 0) {
          this.configViolet2.opacity = 1;
          this.configBlue2.opacity = 1;
          this.configGreen2.opacity = 1;
          this.configYellow2.opacity = 1;
          this.configViolet.opacity = 1;
          this.configBlue.opacity = 1;
          this.configGreen.opacity = 1;
          this.configYellow.opacity = 1;
        }
      }
    },
    telescopeAntiClockwise() {
      if (this.configChavronAntiClockwise.rotation <= -90) {
        this.configChavronAntiClockwise.rotation = -90;
      } else {
        this.configChavronClockwise.rotation = this.configChavronClockwise.rotation - 5;
        this.configTelescope.rotation = this.configTelescope.rotation - 5;
        this.configChavronAntiClockwise.rotation = this.configChavronAntiClockwise.rotation - 5;
        this.configCalibrate.rotation = this.configCalibrate.rotation - 5;
        this.configCalibrateZoom.rotation = this.configCalibrateZoom.rotation - 5;
        
        this.configSlitLight.x = this.configSlitLight.x - 30;

        this.configViolet.x = this.configViolet.x - 10;
        this.configBlue.x = this.configBlue.x - 10;
        this.configGreen.x = this.configGreen.x - 10;
        this.configYellow.x = this.configYellow.x - 10;
        this.configViolet2.x = this.configViolet2.x - 10;
        this.configBlue2.x = this.configBlue2.x - 10;
        this.configGreen2.x = this.configGreen2.x - 10;
        this.configYellow2.x = this.configYellow2.x - 10;

        if(this.configTelescope.rotation < 0){
          this.configViolet2.opacity = 0;
          this.configBlue2.opacity = 0;
          this.configGreen2.opacity = 0;
          this.configYellow2.opacity = 0;
          this.configViolet.opacity = 1;
          this.configBlue.opacity = 1;
          this.configGreen.opacity = 1;
          this.configYellow.opacity = 1;
        }
        else if (this.configTelescope.rotation == 0) {
          this.configViolet2.opacity = 1;
          this.configBlue2.opacity = 1;
          this.configGreen2.opacity = 1;
          this.configYellow2.opacity = 1;
          this.configViolet.opacity = 1;
          this.configBlue.opacity = 1;
          this.configGreen.opacity = 1;
          this.configYellow.opacity = 1;
        }
      }
    },

    ////////////////////////////////////////////////////////////////////////////////////////Table rotation
    tableAntiClockwise() {
      this.configSlitTableLeft.rotation = this.configSlitTableLeft.rotation - 15;
      this.configSlitTableRight.rotation = this.configSlitTableRight.rotation - 15;
      this.configTable.rotation = this.configTable.rotation - 15;
      this.configGratingTable.rotation = this.configGratingTable.rotation - 15;
      // this.configSlitLight.x = this.configSlitLight.x - 30;
      this.configCalibrateZoom.rotation = this.configCalibrateZoom.rotation - 15;
    },
    tableClockwise() {
      this.configSlitTableRight.rotation = this.configSlitTableRight.rotation + 15;
      this.configSlitTableLeft.rotation = this.configSlitTableLeft.rotation + 15;
      this.configTable.rotation = this.configTable.rotation + 15;
      this.configGratingTable.rotation = this.configGratingTable.rotation + 15;
      // this.configSlitLight.x = this.configSlitLight.x + 30;
      this.configCalibrateZoom.rotation = this.configCalibrateZoom.rotation + 15;
    },

    ////////////////////////////////////////////////////////////////////////////////////////Vernier rotation
    vernierAntiClockwise() {
      this.configSlitTableLeft.rotation = this.configSlitTableLeft.rotation - 15;
      this.configSlitTableRight.rotation = this.configSlitTableRight.rotation - 15;
      this.configTable.rotation = this.configTable.rotation - 15;
      this.configVernier.rotation = this.configVernier.rotation - 15;
      this.configVernierLeft.rotation = this.configVernierLeft.rotation - 15;
      this.configVernierRight.rotation = this.configVernierRight.rotation - 15;
      this.configCalibrate.rotation = this.configCalibrate.rotation - 15;
      this.configGratingTable.rotation = this.configGratingTable.rotation - 15;
    },
    vernierClockwise() {
      this.configSlitTableRight.rotation = this.configSlitTableRight.rotation + 15;
      this.configSlitTableLeft.rotation = this.configSlitTableLeft.rotation + 15;
      this.configTable.rotation = this.configTable.rotation + 15;
      this.configVernier.rotation = this.configVernier.rotation + 15;
      this.configVernierLeft.rotation = this.configVernierLeft.rotation + 15;
      this.configVernierRight.rotation = this.configVernierRight.rotation + 15;
      this.configCalibrate.rotation = this.configCalibrate.rotation + 15;
      this.configGratingTable.rotation = this.configGratingTable.rotation + 15;
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.configToggleBtnOff {
  cursor: pointer;
}

.setup {
    width: 100%;
    padding: 20px !important;
}

.bluredImage{
    background: url('../assets/images/bluredImage.jpg') no-repeat center;
    background-size: cover;
    height: 200px;
    filter: blur(0px);
    -webkit-filter: blur(0px);
    -moz-filter: blur(0px);
}

.slit {
  width: 150px;
  height: 150px;
  background: url('../assets/images/slitWrapper.png') no-repeat center;
  background-size: contain;
}

.gap {
  width: 5px ;
  height: 120px;
  margin: 0px auto;
}

@media (min-width: 1500px) {
  .spectrometer {
    flex: 0 0 65% !important;
    max-width: 65% !important;
  }

  .setup {
    flex: 0 0 35% !important;
    max-width: 35% !important;
    padding: 20px !important;
  }
}
</style>
